import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {formatDate } from '@angular/common';
import { ActivatedRoute, ParamMap } from '@angular/router';

import { tap } from 'rxjs/operators';
import { Subscription } from 'rxjs/internal/Subscription';
// import { Observable } from 'rxjs/internal/Observable';

// SERVICES
import { PostService } from 'src/app/shared/service/post.service';
import { UserService } from 'src/app/shared/service/user.service';

// MODEL
import { User } from 'src/app/shared/models/user.model';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit, OnDestroy {
  public postForm: FormGroup ;
  public commentForm: FormGroup ;
  public currentUser: User;
  public isCurrentUser = false;
  public newPost: {user_id: string, message: string, date: string};
  public newComment: { postId: string, userId: string, pseudo: string, date: Date, comment: string };
  public date: any;
  public postArray;
  public userId: string;
  public displayedUser: User;
  public allUsersArray: User[];
  private sub: Subscription;



  constructor(
    private userService: UserService,
    private postService: PostService,
    private fb: FormBuilder,
    public activatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {

    this.postForm = this.fb.group({
      message: [ '', [Validators.required]]
    });

    this.commentForm = this.fb.group({
      message: [ '', [Validators.required]]
    });


    // RECUPERATION ID
    this.sub = this.userService.currentUser.subscribe(
      currentUser => {
        console.log(currentUser);
        this.currentUser = currentUser;
      }
      );
    this.activatedRoute.paramMap.subscribe( (paramMap: ParamMap) => {
      this.userId = paramMap.get('id');

      // Récupération des posts en bdd
      this.postService.getPost(this.userId).subscribe(
        (postArray) => {
          this.postArray = postArray;
          console.log('postArray ', postArray);
        }
      );
      // Le current User est-il le rédacteur des posts?
      this.userService.getAllUsers().subscribe( allUsersArray => {
        this.allUsersArray = allUsersArray;
        this.allUsersArray.forEach( elmt => {
          if (elmt._id === this.userId) {
            if (elmt._id === this.currentUser._id) {
              this.isCurrentUser = true;
            } else {
              this.isCurrentUser = false;
            }
            console.log('TCL: ProfileComponent -> ngOnInit -> this.isCurrentUser', this.isCurrentUser);
            this.displayedUser = elmt;
            console.log('TCL: ProfileComponent -> ngOnInit -> this.displayedUser', this.displayedUser);
          }
        });
      });
    });

    // Mise a jour du behaviour subject postList
    this.postService.postList.subscribe( postListUpdated => {
      this.postArray = postListUpdated;
    });
  }

  public createPost(): void {
    this.date = new Date();
    this.newPost = {
      // tslint:disable-next-line: no-string-literal
      user_id: this.currentUser['_id'],
      message: this.postForm.value.message,
      date: this.date
      // date: formatDate(this.date, 'dd/MM/yyyy', 'fr-FR')
    };
    this.postService.createPost(this.newPost).subscribe( (postListUpdated) => {
      this.postForm.reset();
      // this.postService.postList.next(postListUpdated);
    }, err => {
      console.log('impossible de crée un post', err);
    });
  }

  public createComment(post) {
    this.date = new Date();
    this.newComment = {
      postId : post._id,
      userId : this.currentUser._id,
      pseudo : this.currentUser.pseudo,
      date: this.date,
      comment : this.commentForm.value.message
    };
    this.postService.createComment(this.newComment).subscribe((postListUpdated) => {
      this.postArray = postListUpdated;
      this.commentForm.reset();
    }, err => {
      console.log('impossible de crée un commentaire', err);
    });
  }

  public getProfileUser() {
    this.userService.getProfile(this.displayedUser._id);
  }

  public deletePost(userId: string, postId: string) {
    this.postService.deletePost(userId, postId).subscribe(
      (postListUpdated) => {
        this.postArray = postListUpdated;
      }
    );
  }
  // public deletePost(userId: string, postId: string) {
  //   this.postService.deletePost(userId, postId).pipe(
  //     tap(() =>
  //       this.postArray.forEach(elmt => {
  //         if ( elmt._id === postId ) {
  //           let index = this.postArray.indexOf(elmt);
  //           this.postArray.splice(index, 1);
  //         }
  //       })
  //     )
  //   )
  // }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}
